import React, { useState } from 'react';
import axios from 'axios';

const Admin = () => {
    const [movieData, setMovieData] = useState({
        title: "",
        desc: "",
        rating: 0,
        release: 1950,
        director: "",
        categories: [],
        image: "", // URL input for the image
    });

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        try {
            const response = await axios.post('http://localhost:8000/movie', movieData, {
                headers: {
                    'Content-Type': 'application/json',
                },
            });

            if (response.status === 201) {
                alert("Movie created successfully!");
                setMovieData({
                    title: "",
                    desc: "",
                    rating: 0,
                    release: 1950,
                    director: "",
                    categories: [],
                    image: "",
                });
            }
        } catch (error) {
            console.error("Error:", error);
            alert("Error creating movie");
        }
    };

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setMovieData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const handleTextAreaChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        const { name, value } = e.target;
        setMovieData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    return (
        <div className="m-auto p-7 container text-center">
            <h1>Movie Create Page</h1>
            <form onSubmit={handleSubmit} method="post" className="grid gap-3 justify-center py-5">
                <div className="flex border-b-2 items-center justify-evenly p-4 gap-5">
                    <label htmlFor="title">Movie Title:</label>
                    <input
                        type="text"
                        name="title"
                        id="title"
                        value={movieData.title}
                        onChange={handleInputChange}
                        placeholder="Enter Movie Name"
                        className="border-2"
                    />
                </div>

                <div className="flex border-y-2 items-center justify-between p-4 gap-5">
                    <label htmlFor="desc">Movie Description:</label>
                    <textarea
                        name="desc"
                        id="desc"
                        rows={4}
                        value={movieData.desc}
                        onChange={handleTextAreaChange}
                        cols={25}
                        placeholder="Describe the Movie"
                        className="border-2"
                    />
                </div>

                <div className="flex border-b-2 items-center justify-evenly p-4 gap-5">
                    <label htmlFor="rating">IMDB Ratings:</label>
                    <input
                        type="number"
                        name="rating"
                        id="rating"
                        min={0}
                        max={10}
                        value={movieData.rating}
                        onChange={handleInputChange}
                        className="border-2"
                    />
                </div>

                <div className="flex border-b-2 items-center justify-evenly p-4 gap-5">
                    <label htmlFor="director">Director Name:</label>
                    <input
                        type="text"
                        name="director"
                        id="director"
                        value={movieData.director}
                        onChange={handleInputChange}
                        placeholder="Enter Movie Director Name"
                    />
                </div>

                <div className="flex border-b-2 items-center justify-evenly p-4 gap-5">
                    <label htmlFor="image">Image URL:</label>
                    <input
                        type="text"
                        name="image"
                        id="image"
                        value={movieData.image}
                        onChange={handleInputChange}
                        placeholder="Enter Movie Image URL"
                        className="border-2"
                    />
                </div>

                <button type="submit">Submit</button>
            </form>
        </div>
    );
};

export default Admin;
